import React from 'react'
import axios from 'axios'

import { Container, DefaultImage, InfoContainer, EditBtn, InfoItem, Image } from '../../styles/User'

import { ReactComponent as UserIcon } from '../../assets/icons/usuario_imagen_default.svg';

import { URL } from '../../variables'

function Info({ user, edit }) {

    const logout = async () => {
        const res = await axios({
            url: `${URL}/user/logout`,
            method: 'GET'
        })

        return res.data
    }

    const handleBtn = () => {
        logout().then(() => {
            window.location.replace('/')
        }).catch(e => {
            console.log(e)
            window.location.reload()
        })
    }

    return (
        <Container>
            {user.imagen ? (
                <Image src={user.imagen}/>
            ):(
                <DefaultImage>
                    <UserIcon width="10vw" height="10vw"/>
                </DefaultImage>
            )}
            <InfoContainer>
                <InfoItem>
                    <span>NOMBRE:</span>
                    <p>{user.nombre}</p>
                </InfoItem>
                <InfoItem>
                    <span>EDAD:</span>
                    <p>{user.edad}</p>
                </InfoItem>
                <InfoItem>
                    <span>TIPO DE SANGRE:</span>
                    <p>{user.sangre}</p>
                </InfoItem>
                <InfoItem>
                    <span>DIRECCIÓN:</span>
                    <p>{user.direccion}</p>
                </InfoItem>
                <InfoItem>
                    <span>TELÉFONO:</span>
                    <p>{user.telefono}</p>
                </InfoItem>
                <InfoItem>
                    <span>EMAIL:</span>
                    <p>{user.email}</p>
                </InfoItem>
                <InfoItem>
                    <span>ENFERMEDADES:</span>
                    <p>{user.enfermedades}</p>
                </InfoItem>
                <InfoItem>
                    <span>ENFERMEDADES FAMILIARES:</span>
                    <p>{user.enfermedadesFam}</p>
                </InfoItem>
                <InfoItem>
                    <span>CONTACTO:</span>
                    <p>{user.contacto}</p>
                </InfoItem>
                <InfoItem mgBottom="3vw">
                    <span>ALERGIAS:</span>
                    <p>{user.alergias}</p>
                </InfoItem>
                <EditBtn onClick={handleBtn}>CERRAR SESIÓN</EditBtn>
            </InfoContainer>
            <EditBtn onClick={edit}>EDITAR</EditBtn>
        </Container>
    )
}

export default Info
