import styled from 'styled-components'

const purple = "rgb(88,32, 142)"
const darkBlue = "rgb(0,47,91)"
const lightBlue = "rgb(178,223,252)"

export const Container = styled.div`
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
`

export const ContainerForm = styled.form`
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
`

export const DefaultImage = styled.div`
    width: 15vw;
    height: 15vw;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(240,240,240);
    border: 1px solid ${darkBlue};
`

export const Image = styled.img`
    width: 15vw;
    height: 15vw;
    border-radius: 0.5rem;
    border: 1px solid ${darkBlue};
    object-fit: cover;
`

export const InputImage = styled.div`
    width: 15vw;
    height: 15vw;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(240,240,240);
    border: 1px solid ${darkBlue};
    &:hover{ cursor: pointer; }
`

export const InfoContainer = styled.div`
    width: 50%;
    display: flex;
    flex-direction: column;
`

export const InfoItem = styled.div`
    display: flex;
    align-items: center;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: ${props => props.mgBottom || '1vw'};

    span{
        font-family: MontserratMedium;
        color: ${darkBlue};
        font-size: 1.5vw;
    }

    label{
        font-family: MontserratMedium;
        color: ${darkBlue};
        font-size: 1.5vw;
    }

    p{
        font-family: Montserrat;
        font-size: 1.5vw;
        margin: 0;
        text-align: right;
    }

    input{
        width: 60%;
        font-family: Montserrat;
        font-size: 1vw;
        padding: 0.5vw 1vw;
        background-color: rgb(240,240,240);
        border-radius: 0.5rem;
        border: 0;
        height: 1.5vw;
        &:focus{ outline: 0; }
    }   
`

export const EditBtn = styled.button`
    font-family: MontserratBold;
    color: white;
    font-size: 1vw;
    padding: 0.5vw 1vw;
    background-color: ${darkBlue};
    border-radius: 0.3rem;
    border: 0;
    height: fit-content;
    margin-bottom: 1vw;
    &:hover{ cursor: pointer; }
`

export const BtnContainer = styled.div`
    display: flex;
    flex-direction: column;
`

export const Message = styled.span`
    color: red;
    font-family: MontserratBold;
    font-size: 1.5vw;
    width: 100%;
    text-align: center;
    margin-top: 2vw;
`

export const LoaderContainer = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center !important;
`