import React, { useEffect, useState } from 'react'
import Cookies from 'js-cookie';
import axios from 'axios';

import MainContainer from '../components/MainContainer'
import Info from '../components/user/Info'
import Form from '../components/user/Form'

import { URL } from '../variables'

function User() {

    const [edit, setEdit] = useState(false)
    const [user, setUser] = useState({})

    const getUser = async () => {
        const res = await axios({
            url: `${URL}/user`,
            method: 'GET'
        })

        return res.data
    }

    const startEdit = () => setEdit(true)
    const cancelEdit = () => setEdit(false)

    useEffect(() => {
        if(!Cookies.get('payload')){ window.location.replace('/') }
        getUser().then(({user}) => {
            setUser(user)
        }).catch((e) => {
            console.log(e)
        })
    }, [])

    return (
        <MainContainer title="USUARIO">
            {edit ? <Form user={user} cancelEdit={cancelEdit}/> : <Info user={user} edit={startEdit}/>}
        </MainContainer>
    )
}

export default User
